/* stylelint-disable at-rule-empty-line-before,at-rule-name-space-after,at-rule-no-unknown */
.login-page {
  background: url('~assets/images/bg.jpg');
}
.login-page .login-form {
  width: 430px;
  margin: 0 auto;
}
@media (max-width: 700px) {
  .login-page .login-form {
    width: 100%;
    padding: 0 5%;
  }
}
.login-page .login-form .user-img {
  margin: 20% 0 8% 0;
  font-size: 28px;
  text-align: center;
}
.login-page .login-form .user-img img {
  width: 80px;
}
.login-page .login-form .ant-input-affix-wrapper,
.login-page .login-form .ant-input,
.login-page .login-form .ant-select-selector,
.login-page .login-form .ant-input-group-addon {
  border: none;
  background-color: #f3f6f9;
}
.login-page .login-form .ant-input[type='password'] {
  height: 38px;
}
.login-page .login-form h4.ant-typography {
  margin-bottom: 13%;
}
.login-page .login-form .login-form-forgot {
  position: relative;
  top: 8px;
}
.login-page .login-form a {
  color: #5d78ff;
}
.login-page .login-form a:hover {
  color: #aebcff;
}
.login-page .login-form a:active {
  color: #5d78ff;
}
.login-page .ant-spin-nested-loading > div > .ant-spin {
  max-height: none;
}
